      h1 Combustin

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-7
        p(data-aos="fade-down") Las diferentes sustancias presentes en la naturaleza interactan entre ellas continuamente. Por lo que sucesos tan comunes como la combustin de un fsforo, la oxidacin de un metal en el ambiente o nuestra respiracin obedecen a lo que se conocen como reacciones qumicas. Se trata de un proceso por el cual una o ms sustancias, llamadas reactivos, se transforman en otra u otras sustancias con propiedades diferentes, llamadas productos.

        p(data-aos="fade-down") En una reaccin qumica, los enlaces entre los tomos que forman los reactivos se rompen. Entonces, estos se reorganizan de otro modo, formando unos nuevos y dando lugar a una o ms sustancias diferentes a las iniciales.      
        .bloque-texto-d.color-secundario.p-4
