      .tarjeta.color-acento-botones--borde.p-4(titulo="Ley de conservacin<br> de la masa")
        .row.justify-content-center.align-items-start
          .col-lg-5
            img.img-a.img-t(src='@/assets/curso/temas/109.png') 

          .col-lg-5 
            p Esta Ley establece que, en toda reaccin qumica, la masa de las sustancias que reaccionan es igual a la masa de las sustancias que se forman. 

            p Reaccin qumica en la cual una molcula de metano y dos molculas de oxgeno reaccionan por combustin y se producen dos molculas de agua y una de dixido de carbono. Antes y despus de la reaccin, el nmero de tomos de oxgeno (4), hidrgeno (4) y carbono (1) es el mismo.

            p Por lo tanto, la masa total de los reactivos es la suma de la masa de todos los tomos, lo que quiere decir que es igual a la masa total de los productos.           
      .tarjeta.color-acento-botones--borde.p-4(titulo="Proporciones definidas")
        .row.justify-content-center.align-items-start
