          .col-lg-5 
            p La Ley de las proporciones definidas dice que cuando dos o ms elementos se combinan para formar un compuesto, la relacin entre sus masas es constante.        
      .tarjeta.color-acento-botones--borde.p-4(titulo="Ley de los volmenes<br> de combinacin")
        .row.justify-content-center.align-items-start
