          .col-lg-5 
            p La Ley de los volmenes de combinacin dice que cuando los gases se combinan para obtener compuestos tambin gaseosos, sus respectivos volmenes guardan una proporcin de nmeros enteros sencillos, siempre que se midan en iguales condiciones de presin y temperatura.     
      .tarjeta.color-acento-botones--borde.p-4(titulo="Ley de Avogadro")
        .row.justify-content-center.align-items-start
          .col-lg-5
            img.img-a.img-t(src='@/assets/curso/temas/112.png') 

          .col-lg-5 
            p En 1811, Amadeo Avogadro (1776-1856) explic esta ley partiendo de dos ideas:  En volmenes iguales de distintos gases, a la misma presin y temperatura, existe el mismo nmero de partculas. Las ltimas partculas de los elementos gaseosos no son tomos sino agregados de tomos iguales, a los que llam molculas.  

    Separador 
    #t_3_2.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.2 Ecuaciones qumicas            
    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-6
        p(data-aos="fade-down") Las ecuaciones qumicas constituyen una forma abreviada de expresar por escrito una reaccin qumica, por medio de smbolos y frmulas. En una ecuacin qumica las sustancias que se combinan entre s son los reactivos y se escriben a la izquierda. A la derecha se escriben los productos, que son las sustancias que se forman. Los reactivos se separan de los productos por medio de una flecha, su direccin es de reactivos  productos.

        p(data-aos="fade-down") Los smbolos y frmulas utilizados en la ecuacin #[b deben ir acompaados por un subndice dentro de un parntesis que indica los estados de agregacin de cada sustancia (s) (l) (g).] Tambin se puede colocar el subndice (ac) que significa que la sustancia est en disolucin acuosa. Arriba o debajo de la flecha pueden aparecer las condiciones necesarias para que se efecte la reaccin, por ejemplo, indica que se requiere energa en forma de calor para que se lleve a cabo la reaccin.                                               
      .col-lg-6
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/113.png')

    p(data-aos="fade-down").mb-5 En toda reaccin qumica no se crean ni se destruyen tomos, los que estn presentes se reorganizan para formar nuevas sustancias, conservndose la materia. #[b Recuerda la Ley de conservacin de la materia?] Importante para comprender el equilibrio entre reactivos y productos en una reaccin.

    .row.justify-content-center.align-items-center.mb-5           
