    p(data-aos="fade-down").mb-5 A continuacin, se explican las reacciones segn la transformacin que se produce:

    AcordionA(tipo="b" clase-tarjeta="tarjeta bg25 ")
      .row.justify-content-center.align-items-center(titulo="Reacciones de sntesis") 
        .col-12
          p Son reacciones qumicas en las que dos o ms sustancias (reactivos) se combinan para formar otra distinta (producto), ms compleja. Si los reactivos son elementos, se llaman reacciones de formacin.
 
          p La ecuacin qumica general es:  A + B  AB   
          h5 Ejemplos: 
          p Fe + S  FeS
          p 4 Al + 3 O#[sub 2] 2 Al#[sub 2]O#[sub 3]  
      .row.justify-content-center.align-items-center(titulo="Reacciones de descomposicin") 
        .col-12
          p Son reacciones qumicas en las que un compuesto se descompone en dos o ms sustancias ms simples. Son las inversas de las reacciones de sntesis.
          p La ecuacin qumica general es: 
          p AB  A + B
          h5 Ejemplos:
          p 2 AlCl#[sub 3] 2 Al + 3 Cl#[sub 2]
          p 2 KClO#[sub 3] 2 KCl + 3 O#[sub 2]   
      .row.justify-content-center.align-items-center(titulo="Reacciones de sustitucin o desplazamiento") 
        .col-12
          p Las reacciones de sustitucin o desplazamiento pueden ser simples o dobles.
          p En una reaccin de sustitucin o desplazamiento simple, un elemento es reemplazado por otro elemento ms reactivo en un compuesto para producir un nuevo compuesto y el elemento desplazado.  
          p La ecuacin qumica general es:
          p A + BC  AC + B
          h5 Ejemplos:
          p Fe#[sub 2] O#[sub 3] + 2 Al  Al#[sub 2]O#[sub 3] + 2 Fe
          p 2 Na + 2 H#[sub 2]O  2 NaOH + H#[sub 2]
      .row.justify-content-center.align-items-center(titulo="Reacciones de precipitacin") 
        .col-12
          p En una reaccin de precipitacin, la partcula intercambiada es un in. Se produce, en general, en disolucin acuosa y entre sustancias inicas. En la reaccin, un anin y un catin se ponen en contacto y dan lugar a un compuesto inico insoluble que precipita.
          h5 Ejemplos: 
          p AgNO #[sub 3] (aq) + NaCl (aq)  NaNO #[sub 3] (aq) + AgCl (s)
          p Para saber si se produce una reaccin de este tipo, es necesario saber qu sustancia es insoluble y por esto se ponen en la ecuacin qumica los estados fsicos de las sustancias que intervienen.          
      .row.justify-content-center.align-items-center(titulo="Reacciones de oxidacin-reduccin") 
        .col-12
          p Las reacciones de oxidacin-reduccin (redox) implican la transferencia de electrones entre especies qumicas. Se llaman tambin reacciones de transferencia de electrones, ya que la partcula que se intercambia es el electrn.
          p En una reaccin de oxidacin-reduccin, tienen lugar dos procesos simultneos: la oxidacin y la reduccin.
          p El aluminio reacciona con el oxgeno para formar xido de aluminio,
          p 4 Al + 3 O #[sub 2]  2 Al #[sub 2] O #[sub 3]
          p En el transcurso de esta reaccin, cada tomo de aluminio pierde tres electrones, para formar un ion Al 3+
          p Al  Al 3+ + 3 e -
          p Y cada molcula de O 2 gana cuatro electrones, para formar dos iones O 2-
          p O 2 + 4 e -  2 O 2- 
          p Como los electrones ni se crean ni se destruyen en las reacciones qumicas, la oxidacin y la reduccin son inseparables.
          p El aluminio cede electrones y el oxgeno los gana. El aluminio acta como agente reductor, se oxida (su nmero de oxidacin pasa de 0 a +3) cediendo tres electrones, mientras que el oxgeno acta como agente oxidante, se reduce (su nmero de oxidacin pasa de 0 a -2) ganando dos electrones.

    .row.justify-content-center.align-items-end.mb-5           
      .col-lg-5.my-3
        .row.justify-content-start.pt-5.mb-4          
          .col-auto.bg9.p-3(data-aos="flip-down")
            h5.mb-0(data-aos="fade-down") Reacciones qumicas de inters ambiental.      
        p(data-aos="fade-down").mb-0 Uno de los problemas fundamentales en el presente siglo, que debe ser estudiado por el ciudadano global y que menciona Naciones Unidas, es el cambio climtico y sus consecuencias para la sostenibilidad del planeta y la existencia de la raza humana (Naciones Unidas, s. f.). Por lo anterior, se invita a seguir revisando el contenido del documento con el fin de conocer cules son las reacciones qumicas que afectan directamente el medio ambiente. 
      .col-lg-7.my-3 
