    AcordionA(tipo="b" clase-tarjeta="tarjeta bg25 ")
      .row.justify-content-center.align-items-center(titulo="Reacciones con el oxgeno") 
        .col-12
          p El oxgeno es el elemento qumico ms abundante en la corteza terrestre. Muchos elementos reaccionan con el oxgeno dando lugar a una reaccin de oxidacin. Un caso particular de la reaccin de oxidacin es la denominada reaccin de combustin. 
      .row.justify-content-center.align-items-center(titulo="Reacciones de oxidacin") 
        .col-12
          p En las reacciones de oxidacin, el oxgeno se une al elemento de forma lenta y sin gran desprendimiento de energa, y se forma un compuesto llamado xido: #[b elemento + oxgeno (O2)  xido]

          p La reaccin contraria, en la que el xido se descompone, produce el metal y libera oxgeno, tambin existe y se llama reaccin de reduccin: 

          p calor xido  metal + oxgeno

          p La formacin de xidos no siempre es ventajosa, ya que a veces la oxidacin de algunos no metales, como el carbono y el azufre, puede crear problemas medioambientales. El CO  desprendido contribuye a aumentar el efecto invernadero, y el SO  formado por la oxidacin del azufre favorece la denominada lluvia cida. 

          p Asimismo, la oxidacin de metales, como el hierro, produce grandes perjuicios econmicos. El hierro es uno de los metales que ms se utiliza en la industria y en la construccin, pero en presencia de oxgeno y humedad se oxida rpidamente y se corroe. Para evitarlo, se recubre de una capa de pintura y de una capa de otro metal que no se oxide, por ejemplo, de cromo, lo que se denomina cromado. 
      .row.justify-content-center.align-items-center(titulo="Reacciones de combustin") 
        .col-12
          p Las reacciones de combustin son reacciones de oxidacin en las que una sustancia arde unindose al oxgeno y produce otras sustancias diferentes. La sustancia que arde se denomina combustible y la sustancia que mantiene la combustin se llama comburente. 

          p Uno de los usos ms frecuentes de las reacciones de combustin es la produccin de energa. En la actualidad, los combustibles ms utilizados son los hidrocarburos (compuestos derivados del petrleo formados por carbono e hidrgeno) que, en presencia de oxgeno, producen dixido de carbono, agua y energa. As, por ejemplo, la reaccin de combustin del metano (CH4) es:

          p CH4 (g) + 2 O2 (g)  CO2 (g) + 2H2O (g) + energa 

          p El CO2 desprendido crea problemas medioambientales y contribuye a aumentar el efecto invernadero. Por este motivo, las investigaciones se encaminan a la fabricacin de vehculos no contaminantes, como los coches elctricos. 
      .row.justify-content-center.align-items-center(titulo="Reacciones de combustin en el reino animal") 
        .col-12
          p Metabolismo. El metabolismo es el conjunto de cambios fsicos y qumicos que se originan en los seres vivos y que sirven, por un lado, para mantener sus funciones vitales (anabolismo) y, por otro, para generar la energa que necesitan (catabolismo). As, los alimentos ingeridos, ricos en hidratos de carbono, se descomponen, entre otros compuestos, en glucosa (C6H12O6) que pasa desde el intestino a la sangre y de esta a las clulas donde se producen las reacciones de combustin: 

          p C6H12O6 + 6 O2  6 CO2 + 6 H2O + energa                              

