      h2 3.3 Procesos de combustin 
    .row.justify-content-center.align-items-end.mb-5           
      .col-lg-7
        p(data-aos="fade-down") La combustin no es ms que una reaccin qumica de oxidacin, en la que normalmente se va a liberar una gran cantidad de calor (energa). La reaccin de un elemento qumico con el oxgeno se llama oxidacin. Para entender este tema, se hace necesario adquirir los siguientes conceptos: 
        .bg16.p-5
          ul.lista-ul.fa-ul.mb-0(data-aos="fade-down")
            li 
              i.fas.fa-check.c1
              | Cuando el calor de reaccin es negativo, dicho calor es liberado o entregado por el sistema, y el proceso se llama exotrmico. 
            li 
              i.fas.fa-check.c1
              | Si, en cambio, el calor es positivo, el calor debe ser absorbido por el sistema durante la reaccin qumica y el proceso se denomina endotrmico. 
            li 
              i.fas.fa-check.c1
              | Combustible: toda sustancia (hidrocarburo) capaz de arder. 
            li 
              i.fas.fa-check.c1
              | Comburente: sustancia (aire) que aporta el oxgeno para que el combustible sufra oxidacin.
          p(data-aos="fade-down") En una reaccin de oxidacin, se tiene la siguiente ecuacin:  

          .cajon.color-acento-contenido.mt-4(data-aos="flip-down").p-4.mb-0
            h5.mb-0.text-center(data-aos="fade-down") Combustible + Comburente = Gases de combustin + Energa                                 
