    p(data-aos="fade-down") Entonces, la ecuacin tambin podra tomar la siguiente forma, donde las letras maysculas indican las sustancias qumicas y las minsculas indican la cantidad de moles de dichas sustancias. Y se denominan genricamente reactantes a las sustancias escritas antes del signo igual (A y B) y productos a las escritas despus del signo igual (C y D).

    p(data-aos="fade-down") Se puede dar el caso de que el combustible est formado por una mezcla de sustancias entre las cuales hay una o ms que no reaccionan con el O2, por ejemplo, SO2. En este caso, estas sustancias tambin pasarn en forma completa a los humos (productos). 

