        h5.mb-0(data-aos="fade-down") Tipos de combustin 

    p(data-aos="fade-down").mb-5 Los procesos de combustin se pueden clasificar en: 

    AcordionA(tipo="b" clase-tarjeta="tarjeta bg25 ")
      .row.justify-content-center.align-items-center(titulo="Combustin completa") 
        .col-12
          .row.justify-content-center.align-items-start.mb-5           
            .col-lg-6
              p Las sustancias del combustible se queman hasta el mximo grado posible de oxidacin. En consecuencia, no habr sustancias combustibles en los humos (productos). 

              p En los productos de la combustin completa, se pueden encontrar los siguientes reactantes y sus correspondientes productos:                    
            .col-lg-6 
              .titulo-sexto.color-acento-contenido
                h5 tabla 9.
                span Productos de la combustin 
              img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/131.svg')

          p.mb-5 El balance de materia de las reacciones qumicas se denomina estequiometra. Siempre se plantea en moles, y la sumatoria de masas de ambos miembros siempre deber ser igual, tal como se muestra en el siguiente ejemplo:
          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/132.svg')

      .row.justify-content-center.align-items-center(titulo="Combustin incompleta") 
        .col-12          
          p El combustible no se oxida completamente. Se forman sustancias que todava pueden seguir oxidndose; por ejemplo, CO. Estas sustancias se denominan inquemados. La presencia de inquemados indica que la combustin se est realizando en forma incompleta. Otros inquemados pueden ser H2, CnHm, H2S y C. Estas sustancias son los contaminantes ms comunes que escapan a la atmsfera en los gases de combustin.
   
    Separador 
    #t_3_5.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.5 Balanceo de ecuaciones  

    p(data-aos="fade-down") Balancear una ecuacin es realmente un procedimiento de ensayo y error, que se fundamenta en la bsqueda de diferentes coeficientes numricos que hagan que el nmero de cada tipo de tomos presentes en la reaccin qumica sea el mismo tanto en reactivos como en productos.                  

    .row.justify-content-start.pt-5.mb-4          
      .col-auto.bg9.p-3(data-aos="flip-down")
        h5.mb-0(data-aos="fade-down") Mtodo del tanteo o inspeccin

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-6
        p(data-aos="fade-down") Este mtodo es utilizado para ecuaciones sencillas y consiste en colocar coeficientes a la izquierda de cada sustancia, hasta tener igual nmero de tomos tanto en reactantes como en productos. Observe el siguiente ejemplo: 
        img.img-t.mb-4(data-aos="zoom-in")(src='@/assets/curso/temas/134.svg')
        p(data-aos="fade-down") Para balancearlos hay que colocar un coeficiente 3 al H#[sub 2] y un coeficiente 2 al producto NH#[sub 3]:
        img.img-t.mb-4(data-aos="zoom-in")(src='@/assets/curso/temas/135.svg')
        p(data-aos="fade-down") La ecuacin ha quedado equilibrada, el nmero de tomos de cada elemento es el mismo en reactivos y productos.
                                   
      .col-lg-6 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/133.png')

    .row.justify-content-start.pt-5.mb-4          
      .col-auto.bg9.p-3(data-aos="flip-down")
        h5.mb-0(data-aos="fade-down") Mtodo de xido reduccin
    p(data-aos="fade-down") Las reacciones de oxidacin-reduccin (redox) implican la transferencia de electrones entre especies qumicas. Se llaman tambin reacciones de transferencia de electrones, ya que la partcula que se intercambia es el electrn.

    p(data-aos="fade-down") En una reaccin de oxidacin-reduccin, tienen lugar dos procesos simultneos: la oxidacin y la reduccin. Para balancear una ecuacin por oxidacin-reduccin, hay que tener presente lo registrado a continuacin:


    .row.justify-content-center.align-items-center        
      .col-lg-4
        .tarjeta.tarjeta-avatar.h-100
            img.img-a.img-w(src='@/assets/curso/temas/136.svg' alt='AvatarTop')

            .tarjeta.color-primario.bg28.ts.h-100
              .p-4
                h4.text-center OXIDACIN
                p.mb-0 La oxidacin es el proceso en el cual una especie qumica pierde electrones y su nmero de oxidacin aumenta.     
      .col-lg-4
        .tarjeta.tarjeta-avatar.h-100
            img.img-a.img-w(src='@/assets/curso/temas/136.svg' alt='AvatarTop')

            .tarjeta.color-primario.bg29.ts.h-100
              .p-4
                h4.text-center REDUCCIN
                p.mb-0 La reduccin es el proceso en el cual una especie qumica gana electrones y su nmero de oxidacin disminuye.  


    .bg30.p-5 
      p(data-aos="fade-down").mb-0 Como los procesos de xido-reduccin son de intercambio de electrones, las ecuaciones qumicas estarn igualadas cuando el nmero de electrones cedidos por el agente oxidante sea igual al recibido por el agente reductor. El nmero de electrones intercambiados se calcula fcilmente, teniendo en cuenta la variacin de los nmeros de oxidacin de los elementos. 

    Separador 
    #t_3_6.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 3.6 Estequiometra 
    p(data-aos="fade-down") La estequiometra es la parte de la qumica que estudia las relaciones cuantitativas entre las sustancias que intervienen en una reaccin qumica (reactivos y productos). 
    
    .row.justify-content-center.align-items-end.mb-5           
      .col-lg-6    
        .titulo-sexto.color-acento-contenido
          h5 Figura 9.
          span Reacciones qumicas 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/138.svg')   
      .col-lg-6
        p(data-aos="fade-down") Las relaciones pueden ser: entre reactivos y productos, solo entre reactivos, o solo entre productos. Cualquier clculo estequiomtrico que se lleve a cabo debe hacerse con base en una ecuacin qumica balanceada, para asegurar que el resultado sea correcto. 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/139.png')   

    .row.justify-content-start.pt-5.mb-4          
      .col-auto.bg9.p-3(data-aos="flip-down")
        h5.mb-0(data-aos="fade-down") Clculos estequiomtricos

    p(data-aos="fade-down").mb-5 La estequiometra se emplea para determinar las cantidades de reactivos y productos involucrados en una reaccin qumica. Existe una gran cantidad de problemas relacionados con esta, como se evidencia en el siguiente ejemplo:

    .bg31.p-5
      p(data-aos="fade-down").mb-5 En los vehculos espaciales se utiliza hidrxido de litio slido para eliminar el dixido de carbono exhalado. El hidrxido de litio reacciona con el dixido de carbono gaseoso, formando carbonato de sodio slido y agua lquida. Cuntos gramos de dixido de carbono puede absorber 1 g de hidrxido de litio?    
      .row.justify-content-center.align-items-center.mb-5           
        .col-lg-10
          p(data-aos="fade-down") SOLUCIN

          p(data-aos="fade-down") Anlisis: se da una descripcin verbal de una reaccin y se pide calcular el nmero de gramos de dixido de carbono que reaccionan con 1.00 g de hidrxido de litio.

          p(data-aos="fade-down") Estrategia: se puede usar la descripcin verbal de la reaccin para escribir una ecuacin balanceada: 

          p(data-aos="fade-down") 2LiOH(s) + CO2 (g)          Li2CO3(s) + H2O(l)

          p(data-aos="fade-down") Se dan los gramos de LiOH y se pide calcular los gramos de CO2. Esto puede lograrse mediante la siguiente serie de conversiones: gramos de LiOH moles de LiOH moles de CO2 gramos de CO2. La conversin de gramos de LiOH a moles de LiOH requiere el peso formular de esta sustancia (6.94 +16.00 + 1.01 =23.95). 

          p(data-aos="fade-down") La conversin de moles de LiOH a moles de CO2 se basa en la ecuacin qumica balanceada: 2 mol LiOH 1 mol CO2. 

          p(data-aos="fade-down") Para convertir el nmero de moles de CO2 a gramos, se deber usar el peso formular del CO2:  12.01 +2(16.00) = 44.01.
          .row.justify-content-start.align-items-center         
            .col-auto
              .bgw.p-5.mb-4
                p(data-aos="fade-down").mb-4 RESOLUCIN:
                img.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/140.svg')   
          p(data-aos="fade-down") #[b Comprobacin:] vemos que 23.95  24; 24 _ 2 _ 48 y 44/48 es un poco menor que 1. Por tanto, la magnitud de la respuesta es razonable con base en la cantidad inicial de LiOH; las cifras significativas y las unidades tambin son apropiadas."

    .row.justify-content-center.align-items-end.my-5           
      .col-lg-7
        .row.justify-content-start.pt-5.mb-4          
          .col-auto.bg9.p-3(data-aos="flip-down")
            h5.mb-0(data-aos="fade-down") Clculos de reactivo limitante y porcentaje de rendimiento

        p(data-aos="fade-down") En una reaccin qumica, no necesariamente se consume la totalidad de los reactivos. Generalmente, alguno de ellos se encuentra en exceso. El otro reactivo, que es el que se consume totalmente, se conoce como reactivo limitante. Para que una reaccin se lleve a cabo, debe haber sustancias (reactivos) capaces de reaccionar para formar los productos, pero basta que uno solo de los reactivos se agote para que la reaccin termine. En los procesos industriales, generalmente, se usa un exceso del reactivo ms barato y fcil de conseguir, y se selecciona como limitante el ms caro o difcil de conseguir. 
        h4(data-aos="fade-down") Ejemplo  
        h5(data-aos="fade-down") Considere la reaccin siguiente: 
        p(data-aos="fade-down") Suponga que una disolucin que contiene 3.50 g de Na3PO4, se mezcla con una disolucin que contiene 6.40 g de Ba (NO3)2. #[b Cuntos gramos de Ba2(PO4)2 podrn formarse?]                         
      .col-lg-5
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/141.png')   

    .row.justify-content-center.mb-4.pb-5           
      .col-lg-4.my-3
        .bg32.h-100.p-4.brad
          img.img-t.mb-4(data-aos="zoom-in")(src='@/assets/curso/temas/142.svg')

          p SOLUCIN

          p Anlisis: #[i se da una reaccin qumica y las cantidades de dos reactivos [3.50 g de Na3PO4 y 6.40 g de Ba (NO3)2]. Se pide calcular el nmero de gramos de Ba#[sub 3](PO4)#[sub 2] (uno de los productos).]

          p.mb-0 Estrategia: #[i Se pide calcular la cantidad de producto dadas las cantidades de dos reactivos, as que se trata de un problema de reactivo limitante. Por tanto, primero se debe identificar el reactivo limitante. Para hacerlo, es preciso calcular el nmero de moles de cada reactivo y comparar su proporcin con la que requiere la ecuacin balanceada. Luego, se usa la cantidad del reactivo limitante para calcular la masa de Ba#[sub 3](PO4)#[sub 2] que se forma.]


      .col-lg-8.my-3
        .bg33.h-100.p-4.brad
          img.img-t.mb-4(data-aos="zoom-in")(src='@/assets/curso/temas/142.svg')

          p #[b RESOLUCIN]
          p.fst-italic Se pide calcular la cantidad de producto dadas las cantidades de dos reactivos, as que se trata de un problema de reactivo limitante. Por tanto, primero se debe identificar el reactivo limitante. Para hacerlo, es preciso calcular el nmero de moles de cada reactivo y comparar su proporcin con la que requiere la ecuacin balanceada. Luego, se usa la cantidad del reactivo limitante para calcular la masa de Ba#[sub 3](PO4)#[sub 2] que se forma.

          .row.justify-content-start.align-items-center         
            .col-auto
              .bgw.p-4.mb-4
                p.mb-0 2 mol Na3PO4 = 3 mol Ba (NO3)2 =1 mol Ba3(PO4)2

          p #[b Utilizando la masa molar de cada sustancia, se puede calcular el nmero de moles de cada reactivo:]

          p Estos clculos muestran que el nmero de moles de Ba(NO3)2 es un poco mayor que el de Na3PO4. Sin embargo, los coeficientes de la ecuacin balanceada indican que la reaccin requiere 3 mol Ba(NO3)2 por cada 2 mol Na3PO4. [Es decir, se requieren 1.5 veces ms moles de Ba (NO3)2 que de Na3PO4.]. Es decir, no hay suficiente Ba(NO3)2 para consumir totalmente el Na3PO4. Esto implica que el Ba(NO3)2 es el reactivo limitante. Por tanto, se usa la cantidad de Ba(NO3)2 para calcular la cantidad de producto que se forma. Se podra iniciar el clculo con los gramos de Ba(NO3)2, pero es posible ahorrarse un paso si se parte de los moles de Ba(NO3)2 que se calcularon previamente en l.

    .bg34.mt-5
