          p(data-aos="fade-down") Las unidades son correctas y el nmero de cifras significativas (3) corresponde a las que tiene la cantidad de Ba(NO3)2. 

          h5(data-aos="fade-down") Comentario: 

          p(data-aos="fade-down") Tambin es posible usar la cantidad del reactivo limitante, Ba(NO3)2, para determinar la cantidad de NaNO3 que se forma (4.16 g) y la cantidad de Na3PO4 consumida (2.67 g).
          p(data-aos="fade-down") El nmero de gramos del reactivo en exceso, Na3PO4, que quedan al trmino de la reaccin es igual a la cantidad inicial menos la cantidad consumida en la reaccin:

          .row.justify-content-start.align-items-center         
