    .row.justify-content-start.pt-5.mb-4          
      .col-auto.bg9.p-3(data-aos="flip-down")
        h5.mb-0(data-aos="fade-down") Rendimientos tericos

    .row.justify-content-center.align-items-center.mb-5           
      .col-lg-7
        .cajon.color-acento-contenido.mt-4(data-aos="flip-down").p-4.mb-4
          P.mb-0(data-aos="fade-down") La cantidad de producto que, segn los clculos, se forma cuando reacciona todo el reactivo limitante se llama rendimiento terico. La cantidad de producto que realmente se obtiene en una reaccin se denomina rendimiento real. El rendimiento real casi siempre es menor (y nunca puede ser mayor) que el rendimiento terico.
        p(data-aos="fade-down") Esta diferencia tiene muy diversas causas. Por ejemplo, es posible que una parte de los reactivos no reaccione, o que reaccione de una forma diferente de la deseada (reacciones secundarias). Adems, no siempre es posible recuperar todo el producto de reaccin de la mezcla de reaccin. El porcentaje de rendimiento de una reaccin relaciona el rendimiento real con el terico (calculado): 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/143.svg')

      .col-lg-5
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/144.png')


    .row.justify-content-center.align-items-center.mb-5.pb-5 
      .col-lg-7
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/146.png')              
