      p.fst-italic(data-aos="fade-down") Se pide, primero, calcular el rendimiento terico de un producto (H#[sub 2] C#[sub 6] H#[sub 8] O#[sub 4]) y luego calcular su porcentaje de rendimiento si slo se obtienen, realmente, 33.5 g de la sustancia.

